import api from './data.js'

export default {
    uuid(len, radix) {   //uuid
		var chars = '0123456789ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz'.split('');
		var uuid = [], i;
		radix = radix || chars.length;
		if (len) {
			for (i = 0; i < len; i++) uuid[i] = chars[0 | Math.random()*radix];
		}else{
			var r;
			for(i = 0; i < 12; i++){if(!uuid[i]) {
				r=0|Math.random()*16; uuid[i]=chars[(i == 19) ? (r & 0x3) | 0x8 : r];}
			}
		}
		return uuid.join('');
    },
    setDataSend(result,sblx,kzlx,cbfun){  // 
        let _this = this
        for(let i=0;i<result.length;i++){
            let data = result[i].data
            let dataSend = {}
            dataSend.sblx = sblx;// dd jzq
            dataSend.kzlx = kzlx;// ds() xc() kd() gd() dataSend.func = kdCallBack;//
            dataSend.func = cbfun;//
            dataSend.kzsc = "15";//
            if(sblx == 'dd'){
                dataSend.bwid = result[i].bwid;//id
                dataSend.zzdz = result[i].zzdz;//
                dataSend.txfs = result[i].txfs;//
            }

            if(sblx == 'jzq'){
                dataSend.bwid = data[i].bwid;//id
                dataSend.zzdz = data[i].zzdz;//
                dataSend.txfs = data[i].txfs;//
                dataSend.jzkzqId = data[i].jzkzqId;//id
                dataSend.zcbh = data[i].zcbh;//
            }
            // dataSend.status = 1;//12
            dataSend.opType = 1;
            api.sendMessage(dataSend).then(function(result){
            })
            if(sblx == 'dd'){
                dataSend.func(result[i].bwid,dataSend.kzlx=='kd'?'1':'0')
            }
            if(sblx == 'jzq'){
                dataSend.func(data[i].bwid,dataSend.kzlx=='kd'?'1':'0')
            }
        }
    },

}


