<template>
  <div class="paramcalltest full">
    <head-top headTitle = "" goBack = "true"></head-top>
    <div class = "section scroll-box">
        <div class="con-list-sm padding">
            <light-cell v-for = "(item,index) in msgList" :key="index" :lfCon = "item.title" :rtCon = "item.con"></light-cell>
        </div>
        <div class="con-list-sm padding">
            <light-cell lfCon = ""></light-cell>
            <light-cell v-for = "(item,index) in portOptions" msgIcon = "true" :key="index" :lfCon = "item.lj" :mdCon="item.wl" :blue = "item.state == 1?true:false" :rtCon = "item.state == 1?'':''" zcCon = ""></light-cell>   
        </div>
        <div class="con-list-sm padding">
            <light-cell lfCon = "" zcCon = ""></light-cell>
            <light-cell lfCon = "" mdCon=":2" msgIcon="true"></light-cell>
        </div>
        <div class="con-list-sm padding">
            <light-cell lfCon = "" zcCon = ""></light-cell>
            <light-cell lfCon = "" msgIcon="true"></light-cell>
            <light-cell lfCon = "" msgIcon="true"></light-cell>
        </div>
        <div class="con-list-sm padding">
            <light-cell lfCon = "" msgIcon="true"></light-cell>
            <light-cell lfCon = "" msgIcon="true"></light-cell>
        </div>
    </div>
  </div>
</template>

<script>
import headTop from '@/components/header/mainHeader.vue'
import lightCell from '@/components/common/lightCell'
import iptApen from '@/components/common/iptApen'
import Bus from "@/config/bus.js";

export default {
  data () {
    return {
        msgList:[
            {"title":"","con":"OUN"},
            {"title":"","con":"21212345123213"},
        ],//
        portOptions:[{lj:':0',wl:': 0',state:'1' },{lj:':1',wl:': 1',state:'1' },{lj:':3 ',wl:': 2',state:'false'}],//
        lng:154984238,   //
        lat:586486481,  //
        onValue:'',   //radio
        onOffset:22,    //range
        offValue:'',   //radio
        offOffset:20,    //range
        pole:''// 
    }
  },
  components:{
      headTop,
      lightCell,
      iptApen,
  },
  methods:{
    setPoleMsg(pole){  //
        this.msgList[0].con = pole.dgmc
        this.msgList[1].con = pole.dgh
    },
  },
  created() {
      this.pole = this.$route.query.pole
      this.setPoleMsg(this.pole)
  },
  beforeDestroy() {
      Bus.$emit('paramCallTest',this.pole)
      Bus.$off('paramCallTest')
  },
}
</script>

<style scoped lang = "scss">

</style>



