<template>
<div class="deviceMsg">
  <head-top goBack = "true" headTitle = "">
      <div slot = "save" class="reset-btn" @click = "ToLink('jzqmsgreset')"><i></i></div>
  </head-top>
  <div class="section scroll-box">
    <div class="con-list-sm  padding">
      <light-cell v-for = "(item,index) in msgList" :key="index" :lfCon = "item.title" :rtCon = "item.con"></light-cell>
    </div>
    <ul class="con-list-sm padding">
      <div class="flex_between padding_t"></div>
      <li class="flex_between border dgflag">
        <minute-flag></minute-flag>
        <!-- <div @click.stop = "openPole" class="coverFlag"></div><mt-switch v-model="dengFlag"></mt-switch> -->
        <div class="poleFlag">
          <span @click = "lightOn"></span>
          <span @click = "lightOff"></span>
        </div>
      </li>
      <li class="flex_between border check" v-for = "(item,index) in portOptions">
        <div class="flex_between w55">
          <mt-checklist v-model="portSelect" :options="[item.lj]"></mt-checklist>
          <div>{{item.wl}}</div>
        </div>
        <div :class="[item.state == 1?'font-blue':'font-gray']">{{item.state == 1?'':''}}</div>
      </li>
    </ul>
    <ul class="con-list-sm padding">
      <div class="flex_between padding_t"></div>
      <li class="flex_between border check" v-for = "(item,index) in yaoxinMsg">
        <div class="flex_between w55">
          <mt-checklist v-model="portSelect" :options="[item.lj]"></mt-checklist>
          <div>{{item.wl}}</div>
        </div>
        <div :class="[item.state == 1?'font-blue':'font-gray']">{{item.state == 1?'':''}}</div>
      </li>
    </ul>
    <div style = "height: 2.5rem"></div>
  </div>
  <footer-btn>
    <div slot = 'btn' class="f-btn padding_tb"></div>
    <div slot = 'btn' class="f-btn padding_tb"></div>
    <div slot = 'btn' class="f-btn padding_tb" @click = "ToLink('jzqdatacalltest')"></div>
    <div slot = 'btn' class="f-btn padding_tb" @click = "timeReset"></div>
  </footer-btn>
</div>
</template>

<script>
import { Checklist,Switch } from 'mint-ui'
import headTop from '@/components/header/mainHeader'
import minuteFlag from '@/components/common/minute'
import footerBtn from '@/components/footer/footer'
import lightCell from '@/components/common/lightCell'
import Bus from '@/config/bus.js'
import { Popup,MessageBox,Toast,Indicator } from 'mint-ui'
import api from '../../service/data.js'
import xunce from '../../service/xunce.js'

export default {
data () {
  return {
    pole:'', //
    msgList:[
      {"title":"","con":"OUN"},
      {"title":"","con":"21212345123213"},
      {"title":"","con":"OUN"},
      {"title":"","con":"OUN"},
      {"title":"","con":""},
      {"title":"","con":"09999999-999999"},
    ],//
    portSelect:[], //
    portOptions:[{lj:':0',wl:': 0',state:'1' },{lj:':1',wl:': 1',state:'1' },{lj:':3 ',wl:': 2',state:'0'}], // 
    yaoxinMsg:[{lj:':0',wl:': 0',state:'1' }],
    dengFlag:false, //
    ddkzqIdArr:[], //
  } 
},
components:{
  headTop,
  minuteFlag,
  footerBtn,
  lightCell
},
methods:{
  ToLink(url){   //
    this.$router.push({
      path:'/'+url,
      name:url,
      query:{
        pole:this.pole
      }
    })
  },
  lightSwitch(rwid,ddkzqIdArr,kzlx){    //
    let _this = this
    let data = {}
    data.rwid = rwid
    data.jzkzqIds = ddkzqIdArr.join(',')
    data.kzlx = kzlx
    api.jzqCallTest(data).then(function(result){
      xunce.setDataSend(result,'jzq',kzlx == 1?'kd':'gd',_this.lightCB)
    })
  },
  checkTime(rwid,ddkzqIdArr){  //
    let _this = this
    let data = {}
    data.jzkzqIds = ddkzqIdArr.join(',')
    api.jzqCallTest(data).then(function(result){
      xunce.setDataSend(result,'jzq','ds',_this.checkTimeCB)
    })
  },
  checkTimeCB(bwid){
    let _this = this 
    let data = {}
    data.czlx = 2
    data.bwid = bwid
    let i = 0
    let forRequest =  setInterval(function(){
        console.log(i)
        i++
        if(i<10){
            api.jzqShowCheckTime(data).then(function(result){
                if(result.data.length == _this.ddkzqIdArr.length){
                    clearTimeout(forRequest)
                    Toast(''+result.data[0].bzsj)
                }
            })
        }else{              
          clearInterval(forRequest)
          Toast('')
        }
    },500)
  },
  lightCB(bwid,kzlx){  //
    let _this = this 
    let data = {}
    data.czlx = 2
    data.bwid = bwid
    data.kzlx = kzlx
    console.log(kzlx)
    let i = 0
    let forLight =  setInterval(function(){
        i++
        if(i<10){
            api.jzqShowLightSwitch(data).then(function(result){
                if(result.data.length == _this.ddkzqIdArr.length){
                    clearTimeout(forLight)
                    Toast(result.data.zxjg)
                }
            })
        }else{
            clearInterval(forLight)
            let msg = kzlx == '1'?'':''
            Toast(msg)
        }
    },500)
  },
  lightOn(){    //
    MessageBox.confirm('',{
        message: '',
        showCancelButton: true
    })
    .then(action => { 
        if (action == 'confirm') {
          this.lightSwitch(xunce.uuid(0,12),this.ddkzqIdArr,'1')
        }
    })
  },
  lightOff(){   //
    MessageBox.confirm('',{
        message: '',
        showCancelButton: true
    })
    .then(action => { 
        if (action == 'confirm') {
          this.lightSwitch(xunce.uuid(0,12),this.ddkzqIdArr,'0')
        }
    })
    
  },
  openPole(){  //
    this.dengFlag = !this.dengFlag
  },
  timeReset(){  // 
    this.checkTime(xunce.uuid(0,12),this.ddkzqIdArr)
  },
  setPoleMsg(pole){  //
    this.msgList[0].con = pole.jzkzqmc
    this.msgList[1].con = pole.zcbh
    this.msgList[2].con = pole.azwz
    this.msgList[3].con = pole.ldbmc
    this.msgList[4].con = pole.gldw
    this.msgList[5].con = pole.gylx
  },
  
},
created() {  
  for(let i=0 ;i<this.portOptions.length;i++){ //
    if(this.portOptions[i].state == 1){
      this.portSelect.push(this.portOptions[i].lj)
    }
  }
  this.pole = this.$route.query.pole
  this.setPoleMsg(this.pole)
  let _this = this
  Bus.$on('jzqreset',function(val){  //
    _this.pole = val
    _this.setPoleMsg(_this.pole)
  });
  Bus.$on('jzqparamMsg',function(val){  //
    _this.pole = val
    _this.setPoleMsg(_this.pole)
  })
  Bus.$on('jzqparamCallTest',function(val){  //
    _this.pole = val
    _this.setPoleMsg(_this.pole)
  })
  Bus.$on('jzqdataCallTest',function(val){  //
    _this.pole = val
    _this.setPoleMsg(_this.pole)
  })
  
},
mounted() {
  this.ddkzqIdArr.push(this.pole.jzkzqId)
},
}
</script>

<style scoped lang="scss">
@import '@/style/mixin.scss';
.deviceMsg{
  height: 100%;
  .reset-btn{
    @include wh(100%,auto)
    text-align: right;
    i{
      display: inline-block;
      @include wh(0.58rem,0.58rem)
      @include bg('../../images/common/reset.png')
    }
  }
  .coverFlag{
    position: absolute;
    right: 0.1rem;
    width: 1.58rem;
    height: 0.8rem;
    z-index: 10;
  }
  .poleFlag{
    span{
      @include sc(0.5rem,#169AFF)
      display: inline-block;
      @include wh(0.8rem,0.8rem);
      border:1px solid #169AFF;
      border-radius:2px;
      margin-left:0.23rem;
    }
  }
}
</style>



