const getters = {
    cityArr: state => state.address.cityArr,    //
    cityIndex: state => state.address.cityIndex, //
    quIndex: state => state.address.quIndex,     //
    quName: state => state.address.quName,      //
    loadingSwitch : state => state.loading.loadingSwitch,  //loading
    luIndex: state => state.address.luIndex,    //()
    token: state => state.user.token,
    userInfo: state => state.user.userInfo,
}

export default getters



