<template>
  <div class="deviceMsg">
    <head-top goBack = "true" headTitle = "">
        <div slot = "save" class="reset-btn" @click = "ToLink('devicemsgreset')"><i></i></div>
    </head-top>
    <div class="section scroll-box">
      <div class="con-list-sm  padding">
        <light-cell v-for = "(item,index) in msgList" :key="index" :lfCon = "item.title" :rtCon = "item.con"></light-cell>
      </div>
      <ul class="con-list-sm padding">
        <div class="flex_between padding_t"></div>
        <li class="flex_between border dgflag">
          <minute-flag></minute-flag>
          <!-- <div @click.stop = "openPole" class="coverFlag"></div><mt-switch v-model="dengFlag"></mt-switch> -->
          <div class="poleFlag">
            <span @click = "lightOn"></span>
            <span @click = "lightOff"></span>
          </div>
        </li>
        <li class="flex_between border check" v-for = "(item,index) in portOptions">
          <div class="flex_between w55">
            <mt-checklist v-model="portSelect" :options="[item.lj]"></mt-checklist>
            <div>{{item.wl}}</div>
          </div>
          <div :class="[item.state == 1?'font-blue':'font-gray']">{{item.state == 1?'':''}}</div>
        </li>
      </ul>
      <div style = "height: 2.5rem"></div>
    </div>
    <footer-btn>
      <div slot = 'btn' class="f-btn padding_tb" @click = "ToLink('paramsmsg')"></div>
      <div slot = 'btn' class="f-btn padding_tb" @click = "ToLink('paramcalltest')"></div>
      <div slot = 'btn' class="f-btn padding_tb" @click = "ToLink('datacalltest')"></div>
      <div slot = 'btn' class="f-btn padding_tb" @click = "timeReset"></div>
    </footer-btn>
  </div>
</template>

<script>
import { Checklist,Switch } from 'mint-ui'
import headTop from '@/components/header/mainHeader'
import minuteFlag from '@/components/common/minute'
import footerBtn from '@/components/footer/footer'
import lightCell from '@/components/common/lightCell'
import Bus from '@/config/bus.js'
import { Popup,MessageBox,Toast,Indicator } from 'mint-ui'
import api from '../../service/data.js'
import xunce from '../../service/xunce.js'

export default {
  data () {
    return {
      pole:'', //
      msgList:[
        {"title":"","con":"OUN"},
        {"title":"","con":"21212345123213"},
        {"title":"","con":"OUN"},
        {"title":"","con":"OUN"},
        {"title":"","con":""},
        {"title":"","con":"09999999-999999"},
        {"title":"","con":"201801212-012313"},
        {"title":"","con":""},
        {"title":"","con":""}
      ],//
      portSelect:[], //
      portOptions:[{lj:':0',wl:': 0',state:'1' },{lj:':1',wl:': 1',state:'1' },{lj:':3 ',wl:': 2',state:'0'}], // 
      dengFlag:false, //
      ddkzqIdArr:[], //
    } 
  },
  components:{
    headTop,
    minuteFlag,
    footerBtn,
    lightCell
  },
  methods:{
    ToLink(url){   //
      this.$router.push({
        path:'/'+url,
        name:url,
        query:{
          pole:this.pole
        }
      })
    },
    lightSwitch(rwid,ddkzqIdArr,kzlx){    //
      let _this = this
      let data = {}
      data.rwid = rwid
      data.ddkzqIds = ddkzqIdArr.join(',')
      data.kzlx = kzlx
      api.lightSwitch(data).then(function(result){
        xunce.setDataSend(result,'dd',kzlx == 1?'kd':'gd',_this.lightCB)
      })
    },
    checkTime(rwid,ddkzqIdArr){  //
      let _this = this
      let data = {}
      data.rwid = rwid
      data.ddkzqIds = ddkzqIdArr.join(',')
      api.checkTime(data).then(function(result){
        xunce.setDataSend(result,'dd','ds',_this.checkTimeCB)
      })
    },
    checkTimeCB(bwid){
      let _this = this 
      let data = {}
      data.czlx = 2
      data.bwid = bwid
      let i = 0
      let forRequest =  setInterval(function(){
          console.log(i)
          i++
          if(i<10){
              api.showCheckTime(data).then(function(result){
                  if(result.data.length == _this.ddkzqIdArr.length){
                      clearTimeout(forRequest)
                      Toast(''+result.data[0].bzsj)
                  }
              })
          }else{              
            clearInterval(forRequest)
            Toast('')
          }
      },500)
    },
    lightCB(bwid,kzlx){  //
      let _this = this 
      let data = {}
      data.czlx = 2
      data.bwid = bwid
      data.kzlx = kzlx
      console.log(kzlx)
      let i = 0
      let forLight =  setInterval(function(){
          i++
          if(i<10){
              api.showLightSwitch(data).then(function(result){
                  if(result.data.length == _this.ddkzqIdArr.length){
                      clearTimeout(forLight)
                      Toast(result.data.zxjg)
                  }
              })
          }else{
              clearInterval(forLight)
              let msg = kzlx == '1'?'':''
              Toast(msg)
          }
      },500)
    },
    lightOn(){    //
      MessageBox.confirm('',{
          message: '',
          showCancelButton: true
      })
      .then(action => { 
          if (action == 'confirm') {
            this.lightSwitch(xunce.uuid(0,12),this.ddkzqIdArr,'1')
          }
      })
    },
    lightOff(){   //
      MessageBox.confirm('',{
          message: '',
          showCancelButton: true
      })
      .then(action => { 
          if (action == 'confirm') {
            this.lightSwitch(xunce.uuid(0,12),this.ddkzqIdArr,'0')
          }
      })
      
    },
    openPole(){  //
      this.dengFlag = !this.dengFlag
    },
    timeReset(){  // 
      this.checkTime(xunce.uuid(0,12),this.ddkzqIdArr)
      // var now = new Date();
      // var year = now.getFullYear();       //
      // var month = now.getMonth() + 1;     //
      // var day = now.getDate();            //
      // var hh = now.getHours();            //
      // var mm = now.getMinutes();          //
      // var ss = now.getSeconds();           //
      // var clock = year + "-";
      // if(month < 10)
      //     clock += "0";
      // clock += month + "-";
      // if(day < 10)
      //     clock += "0";
      // clock += day + " ";
      // if(hh < 10)
      //     clock += "0";
      // clock += hh + ":";
      // if (mm < 10) clock += '0'; 
      // clock += mm + ":"; 
      // if (ss < 10) clock += '0'; 
      // clock += ss;  
      // Toast(':'+clock)
    },
    setPoleMsg(pole){  //
      this.msgList[0].con = pole.dgmc
      this.msgList[1].con = pole.dgh
      this.msgList[2].con = pole.jjxmc
      this.msgList[3].con = pole.ldbmc
      this.msgList[4].con = pole.txfs
      this.msgList[5].con = pole.zcbhDgglq
      this.msgList[6].con = pole.zcbh
      this.msgList[7].con = pole.jzkzqmc
      this.msgList[8].con = pole.azwz
    },
    
  },
  created() {  
    for(let i=0 ;i<this.portOptions.length;i++){ //
      if(this.portOptions[i].state == 1){
        this.portSelect.push(this.portOptions[i].lj)
      }
    }
    this.pole = this.$route.query.pole
    this.setPoleMsg(this.pole)
    let _this = this
    Bus.$on('reset',function(val){  //
      _this.pole = val
      _this.setPoleMsg(_this.pole)
    });
    Bus.$on('paramMsg',function(val){  //
      _this.pole = val
      _this.setPoleMsg(_this.pole)
    })
    Bus.$on('paramCallTest',function(val){  //
      _this.pole = val
      _this.setPoleMsg(_this.pole)
    })
    Bus.$on('dataCallTest',function(val){  //
      _this.pole = val
      _this.setPoleMsg(_this.pole)
    })
    
  },
  mounted() {
    this.ddkzqIdArr.push(this.pole.ddkzqId)
  },
  // beforeRouteLeave (to, from, next) {
  //   if(to.name != 'singlelightmsg'){
  //       from.meta.isBack = true
  //       next()
  //   }else{
  //       from.meta.isBack = false;
  //       next()
  //   }
  // },
  // activated() { //keepalive 
  //     if(!this.$route.meta.isBack){
  //       this.setPoleMsg()
  //     }else{
  //       Bus.$on('reset',function(val){
  //         console.log(val);
  //       });
  //     }
  // },
}
</script>

<style scoped lang="scss">
@import '@/style/mixin.scss';
.deviceMsg{
    height: 100%;
    .reset-btn{
      @include wh(100%,auto)
      text-align: right;
      i{
        display: inline-block;
        @include wh(0.58rem,0.58rem)
        @include bg('../../images/common/reset.png')
      }
    }
    .coverFlag{
      position: absolute;
      right: 0.1rem;
      width: 1.58rem;
      height: 0.8rem;
      z-index: 10;
    }
    .poleFlag{
      span{
        @include sc(0.5rem,#169AFF)
        display: inline-block;
        @include wh(0.8rem,0.8rem);
        border:1px solid #169AFF;
        border-radius:2px;
        margin-left:0.23rem;
      }
    }
}
</style>



